.admin-container {
  display: flex;
  min-height: 100vh;
  background: #f7f8fa;
}
.dashboard-cards {
  display: flex;
  gap: 24px;
  margin: 32px 0 24px 0;
}
.card {
  flex: 1;
  background: #eaf3ff;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(32,64,128,0.04);
  padding: 24px 0 18px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 180px;
  min-height: 110px;
}
.card .icon {
  font-size: 2.2rem;
  margin-bottom: 8px;
  color: #204080;
}
.card.classes {
  background: #204080;
  color: #fff;
}
.card.classes .icon {
  color: #fff;
}
.card.suspended {
  background: #4eaaff;
  color: #fff;
}
.card.suspended .icon {
  color: #fff;
}
.card .count {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 4px;
}
.card .desc {
  font-size: 1.05rem;
  opacity: 0.95;
}
.class-section {
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(32,64,128,0.04);
  padding: 24px;
  margin-top: 18px;
}
.class-header-row {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}
.add-class-btn {
  display: flex;
  align-items: center;
  gap: 8px;
  background: #204080;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 8px 18px;
  font-size: 1.08rem;
  font-weight: 500;
  cursor: pointer;
  box-shadow: 0 1px 4px rgba(32,64,128,0.07);
  transition: background 0.18s, color 0.18s;
}
.add-class-btn:hover, .add-class-btn:focus {
  background: #4eaaff;
  color: #fff;
}
.class-table-wrapper {
  width: 100%;
  overflow-x: auto;
}
.class-table {
  width: 100%;
  border-collapse: collapse;
  min-width: 600px;
  background: #fff;
}
.class-table th, .class-table td {
  text-align: left;
  padding: 10px 8px;
  font-size: 1rem;
}
.class-table th {
  color: #888;
  font-weight: 500;
  background: #f7f8fa;
}
.class-table tr:nth-child(even) td {
  background: #f7f8fa;
}
.class-table th:last-child, .class-table td:last-child {
  text-align: center;
}
.actions {
  display: flex;
  gap: 10px;
}
.action-btn {
  background: none;
  border: 1px solid transparent;
  cursor: pointer;
  font-size: 1.1rem;
  padding: 6px 8px;
  border-radius: 6px;
  transition: background 0.18s, color 0.18s, border-color 0.18s, box-shadow 0.18s;
}
.action-btn.edit {
  color: #1d4ed8;
}
.action-btn.delete {
  color: #dc2626;
}
.action-btn.edit:hover {
  background: #eef2ff;
  border-color: #c7d2fe;
  box-shadow: 0 1px 3px rgba(29,78,216,0.15);
}
.action-btn.delete:hover {
  background: #fef2f2;
  border-color: #fecaca;
  box-shadow: 0 1px 3px rgba(220,38,38,0.15);
}

/* Confirm modal actions */
.modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-top: 18px;
}
.modal-btn {
  border: none;
  border-radius: 6px;
  padding: 10px 16px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
}
.modal-btn.cancel {
  background: #f3f4f6;
  color: #374151;
}
.modal-btn.cancel:hover {
  background: #e5e7eb;
}
.modal-btn.confirm {
  background: #dc2626;
  color: #fff;
}
.modal-btn.confirm:hover {
  background: #b91c1c;
}
.checkbox-group {
  display: flex;
  flex-wrap: wrap;
  gap: 8px 18px;
  margin-bottom: 10px;
}
.checkbox-label {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 1rem;
  color: #204080;
  font-weight: 500;
}
/* Modal and responsiveness styles same as AdminStudent.css */
.modal-overlay {
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  background: rgba(0,0,0,0.18);
  z-index: 2000;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: fadeIn 0.2s;
}
.modal-content {
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 32px rgba(32,64,128,0.13);
  padding: 32px 28px 24px 28px;
  min-width: 320px;
  max-width: 98vw;
  width: 100%;
  max-width: 520px;
  position: relative;
  animation: modalPop 0.25s cubic-bezier(.4,0,.2,1);
  max-height: 90vh;
  overflow-y: auto;
}
@keyframes modalPop {
  from { opacity: 0; transform: translateY(30px) scale(0.98); }
  to { opacity: 1; transform: translateY(0) scale(1); }
}
.modal-close {
  position: absolute;
  top: 16px;
  right: 16px;
  background: none;
  border: none;
  font-size: 1.3rem;
  color: #888;
  cursor: pointer;
  z-index: 2;
  transition: color 0.18s;
}
.modal-close:hover {
  color: #204080;
}
.student-modal-form {
  display: flex;
  flex-direction: column;
  gap: 0;
}
.student-modal-form .form-title {
  color: #204080;
  text-align: center;
  font-size: 1.25rem;
  font-weight: 500;
  margin-bottom: 18px;
}
.student-modal-form .input-label {
  color: #444;
  font-size: 1rem;
  margin-bottom: 4px;
  margin-top: 10px;
}
.student-modal-form .input-field {
  padding: 10px 12px;
  border: 1px solid #d1d5db;
  border-radius: 4px;
  font-size: 1rem;
  margin-bottom: 6px;
  outline: none;
  transition: border 0.2s;
}
.student-modal-form .input-field:focus {
  border: 1.5px solid #204080;
}
.student-modal-form .signup-btn {
  background: #204080;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 12px 0;
  font-size: 1.1rem;
  font-weight: 500;
  cursor: pointer;
  margin-top: 14px;
  transition: background 0.2s;
}
.student-modal-form .signup-btn:hover {
  background: #4eaaff;
}
.error-message {
  color: #e53e3e;
  background: #fff0f0;
  border-radius: 4px;
  padding: 7px 0 7px 10px;
  margin: 8px 0 0 0;
  font-size: 1rem;
}
@media (min-width: 700px) {
  .student-modal-form .modal-form-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 18px 24px;
  }
  .student-modal-form .modal-form-grid > div {
    display: flex;
    flex-direction: column;
  }
  .modal-content {
    max-width: 700px;
    min-width: 520px;
  }
}
.student-modal-form .modal-form-grid {
  display: flex;
  flex-direction: column;
  gap: 0;
}
@media (max-width: 700px) {
  .modal-content {
    display: flex;
    flex-direction: column;
    height: 100%;
    min-height: 60vh;
    max-height: 95vh;
    overflow-y: auto;
    box-sizing: border-box;
  }
  .student-modal-form {
    display: flex;
    flex-direction: column;
    height: 100%;
    min-height: 60vh;
    max-height: 95vh;
    overflow-y: auto;
  }
  .student-modal-form .signup-btn {
    position: sticky;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    margin-top: 18px;
    z-index: 2;
    background: #204080;
    color: #fff;
    border-radius: 0 0 8px 8px;
    font-size: 1.1rem;
  }
}
@media (max-width: 600px) {
  .modal-content {
    display: flex;
    flex-direction: column;
    height: 100%;
    min-height: 60vh;
    max-height: 97vh;
    overflow-y: auto;
    box-sizing: border-box;
  }
  .student-modal-form {
    display: flex;
    flex-direction: column;
    height: 100%;
    min-height: 60vh;
    max-height: 97vh;
    overflow-y: auto;
  }
  .student-modal-form .signup-btn {
    position: sticky;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    margin-top: 16px;
    z-index: 2;
    background: #204080;
    color: #fff;
    border-radius: 0 0 8px 8px;
    font-size: 1rem;
  }
}
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0,0,0,0.15);
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
}
.modal {
  background: #fff;
  border-radius: 8px;
  padding: 24px;
  min-width: 320px;
  max-width: 90vw;
  box-shadow: 0 2px 16px rgba(0,0,0,0.12);
  position: relative;
}
/* --- Cool Dropdown Icon Styles ONLY --- */
.menu-item .dropdown-icon {
  font-size: 1.1rem;
  color: #F59E0B;
  margin-left: 8px;
  transition: transform 0.22s cubic-bezier(.4,0,.2,1), color 0.18s;
  display: flex;
  align-items: center;
}
.menu-item.active .dropdown-icon, .menu-item:hover .dropdown-icon {
  color: #fff;
}