.admin-container {
  display: flex;
  min-height: 100vh;
  background: #f7f8fa;
}

.sidebar {
  width: 240px;
  background: #204080;
  color: #fff;
  display: flex;
  flex-direction: column;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  z-index: 1000;
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  transform: translateX(0);
}
.sidebar .sidebar-header {
  height: 64px;
  display: flex;
  align-items: center;
  padding: 0 24px;
  font-size: 1.3rem;
  font-weight: bold;
  background: #f7f8fa;
  color: #204080;
  letter-spacing: 1px;
  gap: 10px;
}
.sidebar-logo {
  width: 32px;
  height: 32px;
  object-fit: contain;
  margin-right: 8px;
}
.logo-text {
  font-size: 1.2rem;
  font-weight: bold;
  color: #204080;
}
.menu {
  flex: 1;
  display: flex;
  flex-direction: column;
  margin-top: 24px;
}
.menu-item {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 12px 24px;
  font-size: 1.05rem;
  cursor: pointer;
  border-radius: 6px;
  margin: 2px 12px;
  transition: background 0.2s;
}
.menu-item.active,
.menu-item:hover {
  background: #4669b3;
}
.menu-item .icon {
  font-size: 1.2rem;
}

.main-content {
  flex: 1;
  margin-left: 240px;
  padding: 0 24px 24px 24px;
  width: 100%;
}

.admin-header {
  height: 64px;
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 24px;
  border-bottom: 1px solid #e5e7eb;
  position: sticky;
  top: 0;
  z-index: 10;
}

.admin-header-left {
  display: flex;
  align-items: center;
  gap: 10px;
}

.menu-toggle {
  display: none;
  background: none;
  border: none;
  font-size: 1.6rem;
  color: #204080;
  cursor: pointer;
}
.year-select {
  background: #f7f8fa;
  border-radius: 6px;
  padding: 6px 18px;
  font-size: 1.1rem;
  color: #204080;
  border: 1px solid #e5e7eb;
  outline: none;
  transition: border 0.2s;
  min-width: 150px;
  max-width: 180px;
  font-weight: 500;
  appearance: none;
  cursor: pointer;
}
.year-select:focus {
  border: 1.5px solid #204080;
}
.admin-actions {
  display: flex;
  align-items: center;
  gap: 18px;
}
.admin-actions .icon {
  position: relative;
  font-size: 1.3rem;
  color: #204080;
  cursor: pointer;
}
.admin-actions .badge {
  position: absolute;
  top: -7px;
  right: -7px;
  background: #e53e3e;
  color: #fff;
  font-size: 0.7rem;
  border-radius: 50%;
  padding: 2px 6px;
  font-weight: bold;
}
.admin-actions .badge.orange {
  background: #f6ad55;
  color: #fff;
}
.admin-name {
  font-weight: 500;
  color: #204080;
  margin-left: 8px;
}

.dashboard-cards {
  display: flex;
  gap: 24px;
  margin: 32px 0 24px 0;
}
.card {
  flex: 1;
  background: #eaf3ff;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(32, 64, 128, 0.04);
  padding: 24px 0 18px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 180px;
  min-height: 110px;
}
.card .icon {
  font-size: 2.2rem;
  margin-bottom: 8px;
  color: #204080;
}
.card.students {
  background: #4eaaff;
  color: #fff;
}
.card.students .icon {
  color: #fff;
}
.card.fees {
  background: #1b3a4b;
  color: #fff;
}
.card.fees .icon {
  color: #fff;
}
.card.discipline {
  background: #204080;
  color: #fff;
}
.card.discipline .icon {
  color: #fff;
}
.card .count {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 4px;
}
.card .desc {
  font-size: 1.05rem;
  opacity: 0.95;
}

.student-section {
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(32, 64, 128, 0.04);
  padding: 24px;
  margin-top: 18px;
}
.student-header-row {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}
.add-student-btn {
  display: flex;
  align-items: center;
  gap: 8px;
  background: #204080;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 8px 18px;
  font-size: 1.08rem;
  font-weight: 500;
  cursor: pointer;
  box-shadow: 0 1px 4px rgba(32, 64, 128, 0.07);
  transition: background 0.18s, color 0.18s;
}
.add-student-btn:hover,
.add-student-btn:focus {
  background: #4eaaff;
  color: #fff;
}

.student-table-wrapper {
  width: 100%;
  overflow-x: auto;
}
.student-table {
  width: 100%;
  border-collapse: collapse;
  min-width: 900px;
  background: #fff;
}
.student-table th,
.student-table td {
  text-align: left;
  padding: 10px 8px;
  font-size: 1rem;
}
.student-table th {
  color: #888;
  font-weight: 500;
  background: #f7f8fa;
}
.student-table tr:nth-child(even) td {
  background: #f7f8fa;
}
.student-table th:last-child, .student-table td:last-child {
  text-align: center;
}
.actions {
  display: flex;
  gap: 10px;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.action-btn {
  background: none;
  border: 1px solid transparent;
  cursor: pointer;
  font-size: 1.1rem;
  padding: 6px 8px;
  border-radius: 6px;
  transition: background 0.18s, color 0.18s, border-color 0.18s, box-shadow 0.18s;
}
.action-btn.edit {
  color: #1d4ed8;
}
.action-btn.delete {
  color: #dc2626;
}
.action-btn.edit:hover {
  background: #eef2ff;
  border-color: #c7d2fe;
  box-shadow: 0 1px 3px rgba(29,78,216,0.15);
}
.action-btn.delete:hover {
  background: #fef2f2;
  border-color: #fecaca;
  box-shadow: 0 1px 3px rgba(220,38,38,0.15);
}

/* Sidebar overlay for mobile */
.sidebar-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.18);
  z-index: 999;
  animation: fadeIn 0.2s;
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Responsive styles */
@media (max-width: 900px) {
  .dashboard-cards {
    flex-direction: column;
    gap: 18px;
  }
}
@media (max-width: 700px) {
  .sidebar {
    transform: translateX(-100%);
    position: fixed;
    left: 0;
    top: 0;
    height: 100vh;
    box-shadow: 2px 0 8px rgba(32, 64, 128, 0.08);
  }
  .sidebar.open {
    transform: translateX(0);
  }
  .menu-toggle {
    display: block;
  }
  .main-content {
    margin-left: 0;
    padding: 0 8px 12px 8px;
    min-width: 0;
    box-sizing: border-box;
  }
  .admin-header {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    gap: 0;
    padding: 8px 4px 0 4px;
    min-width: 0;
  }
  .admin-header-left {
    display: flex;
    align-items: center;
    gap: 6px;
    min-width: 0;
  }
  .year-select {
    width: auto;
    min-width: 0;
    max-width: 100px;
    font-size: 0.98rem;
    margin: 0;
  }
  .admin-actions {
    width: auto;
    justify-content: flex-end;
    gap: 8px;
    margin-bottom: 0;
    flex-shrink: 0;
  }
  .admin-name {
    font-size: 0.98rem;
    margin-left: 4px;
  }
  .student-section {
    padding: 8px;
    margin-top: 10px;
  }
  .student-table-wrapper {
    padding: 0;
  }
  .student-table {
    min-width: 600px;
    font-size: 0.98rem;
  }
}

.logout-btn {
  display: flex;
  align-items: center;
  gap: 10px;
  width: calc(100% - 32px);
  margin: 18px 16px 12px 16px;
  padding: 10px 0;
  background: #fff;
  color: #204080;
  border: none;
  border-radius: 6px;
  font-size: 1.08rem;
  font-weight: 600;
  cursor: pointer;
  box-shadow: 0 1px 4px rgba(32, 64, 128, 0.07);
  transition: background 0.18s, color 0.18s;
  justify-content: center;
}
.logout-btn:hover,
.logout-btn:focus {
  background: #eaf3ff;
  color: #1b3a4b;
}
.logout-icon {
  font-size: 1.2rem;
}

@media (max-width: 700px) {
  .logout-btn {
    width: calc(100% - 24px);
    margin: 14px 12px 10px 12px;
    font-size: 1rem;
    padding: 9px 0;
  }
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.18);
  z-index: 2000;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: fadeIn 0.2s;
}
.modal-content {
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 32px rgba(32, 64, 128, 0.13);
  padding: 32px 28px 24px 28px;
  min-width: 320px;
  max-width: 98vw;
  width: 100%;
  max-width: 520px;
  position: relative;
  animation: modalPop 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  max-height: 90vh;
  overflow-y: auto;
}
@keyframes modalPop {
  from {
    opacity: 0;
    transform: translateY(30px) scale(0.98);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}
.modal-close {
  position: absolute;
  top: 16px;
  right: 16px;
  background: none;
  border: none;
  font-size: 1.3rem;
  color: #888;
  cursor: pointer;
  z-index: 2;
  transition: color 0.18s;
}
.modal-close:hover {
  color: #204080;
}
.student-modal-form {
  display: flex;
  flex-direction: column;
  gap: 0;
}
.student-modal-form .form-title {
  color: #204080;
  text-align: center;
  font-size: 1.25rem;
  font-weight: 500;
  margin-bottom: 18px;
}
.student-modal-form .input-label {
  color: #444;
  font-size: 1rem;
  margin-bottom: 4px;
  margin-top: 10px;
}
.student-modal-form .input-field {
  padding: 10px 12px;
  border: 1px solid #d1d5db;
  border-radius: 4px;
  font-size: 1rem;
  margin-bottom: 6px;
  outline: none;
  transition: border 0.2s;
}
.student-modal-form .input-field:focus {
  border: 1.5px solid #204080;
}
.student-modal-form .signup-btn {
  background: #204080;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 12px 0;
  font-size: 1.1rem;
  font-weight: 500;
  cursor: pointer;
  margin-top: 14px;
  transition: background 0.2s;
}
.student-modal-form .signup-btn:hover {
  background: #4eaaff;
}
.error-message {
  color: #e53e3e;
  background: #fff0f0;
  border-radius: 4px;
  padding: 7px 0 7px 10px;
  margin: 8px 0 0 0;
  font-size: 1rem;
}
@media (min-width: 700px) {
  .student-modal-form .modal-form-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 18px 24px;
  }
  .student-modal-form .modal-form-grid > div {
    display: flex;
    flex-direction: column;
  }
  .modal-content {
    max-width: 700px;
    min-width: 520px;
  }
}
.student-modal-form .modal-form-grid {
  display: flex;
  flex-direction: column;
  gap: 0;
}
@media (max-width: 699px) {
  .modal-content {
    padding: 12px 2vw 12px 2vw;
    min-width: 0;
    max-width: 98vw;
    width: 100vw;
    max-height: 98vh;
    overflow-y: auto;
  }
  .student-modal-form .modal-form-grid {
    display: flex;
    flex-direction: column;
    gap: 0;
  }
  .student-modal-form .modal-form-grid > div {
    display: flex;
    flex-direction: column;
    gap: 0;
  }
  .student-modal-form .modal-form-grid > div > * {
    width: 100%;
    margin-right: 0;
  }
}
@media (max-width: 600px) {
  .modal-content {
    padding: 18px 4vw 18px 4vw;
    min-width: 0;
    max-width: 98vw;
    width: 100vw;
  }
  .student-modal-form .form-title {
    font-size: 1.08rem;
  }
  .student-modal-form .input-label,
  .student-modal-form .input-field {
    font-size: 0.98rem;
  }
}

.add-student-fab {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: #204080;
  color: #fff;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 16px rgba(32, 64, 128, 0.18);
  cursor: pointer;
  font-size: 2rem;
  transition: background 0.18s, box-shadow 0.18s;
}
.add-student-fab:hover,
.add-student-fab:focus {
  background: #4eaaff;
  box-shadow: 0 6px 24px rgba(32, 64, 128, 0.22);
  color: #fff;
}

.student-search-bar {
  margin-left: 0;
  margin-right: 0;
  width: 100%;
  max-width: 340px;
  min-width: 180px;
  box-sizing: border-box;
  font-size: 1rem;
  transition: box-shadow 0.18s;
}
.upload-many-btn,
.print-list-btn {
  margin-left: 0;
  margin-right: 0;
  font-size: 1rem;
  display: flex;
  align-items: center;
  gap: 8px;
  white-space: nowrap;
}
@media (max-width: 700px) {
  .student-search-bar {
    min-width: 120px;
    max-width: 100vw;
    font-size: 0.98rem;
    padding: 7px 10px;
  }
  .upload-many-btn,
  .print-list-btn {
    font-size: 0.98rem;
    padding: 7px 10px;
  }
}

/* Professional Pagination Styles */
.pagination-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 0;
  flex-wrap: wrap;
  gap: 16px;
  border-top: 1px solid #e0e0e0;
  margin-top: 16px;
}

.pagination-items-per-page {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  color: #666;
}

.pagination-select {
  padding: 6px 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 14px;
  background: #fff;
  cursor: pointer;
  transition: border-color 0.2s ease;
}

.pagination-select:focus {
  outline: none;
  border-color: #1976d2;
}

.pagination-info {
  font-size: 14px;
  color: #666;
  text-align: center;
}

.pagination-controls {
  display: flex;
  align-items: center;
  gap: 8px;
}

.pagination-btn {
  padding: 6px 12px;
  border: 1px solid #ddd;
  border-radius: 4px;
  background: #fff;
  color: #333;
  cursor: pointer;
  font-size: 14px;
  transition: all 0.2s ease;
}

.pagination-btn:hover:not(:disabled) {
  background: #f5f5f5;
  border-color: #bbb;
}

.pagination-btn:disabled {
  background: #f5f5f5;
  color: #ccc;
  cursor: not-allowed;
}

.pagination-numbers {
  display: flex;
  gap: 4px;
}

.pagination-number {
  padding: 6px 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  background: #fff;
  color: #333;
  cursor: pointer;
  font-size: 14px;
  transition: all 0.2s ease;
  min-width: 32px;
  text-align: center;
}

.pagination-number:hover {
  background: #f5f5f5;
  border-color: #bbb;
}

.pagination-number.active {
  background: #1976d2;
  border-color: #1976d2;
  color: #fff;
}

.pagination-number.active:hover {
  background: #1565c0;
  border-color: #1565c0;
}

/* Pagination Responsive Styles */
@media (max-width: 768px) {
  .pagination-container {
    flex-direction: column;
    gap: 12px;
    padding: 12px 0;
  }
  
  .pagination-container > div:first-child {
    order: 2;
  }
  
  .pagination-container > div:nth-child(2) {
    order: 1;
  }
  
  .pagination-container > div:last-child {
    order: 3;
    width: 100%;
    justify-content: center;
  }
  
  .pagination-btn {
    padding: 8px 12px;
    font-size: 13px;
  }
  
  .pagination-select {
    padding: 8px 10px;
    font-size: 13px;
  }
}

@media (max-width: 480px) {
  .pagination-container {
    gap: 8px;
    padding: 8px 0;
  }
  
  .pagination-btn {
    padding: 6px 10px;
    font-size: 12px;
    min-width: 28px;
  }
  
  .pagination-select {
    padding: 6px 8px;
    font-size: 12px;
  }
  
  .pagination-number {
    padding: 6px 8px;
    font-size: 12px;
    min-width: 28px;
  }
  
  .pagination-container > div {
    font-size: 12px;
  }
}

/* Print styles for class list */
@media print {
  body * {
    visibility: hidden !important;
  }
  .print-class-list-area,
  .print-class-list-area * {
    visibility: visible !important;
  }
  .print-class-list-area {
    position: absolute;
    left: 0;
    top: 0;
    width: 100vw;
    min-width: 100vw;
    background: #fff;
    padding: 24px 18px;
    box-sizing: border-box;
    font-size: 15px;
    color: #222;
  }
  @page {
    size: A4 landscape;
    margin: 18mm 10mm 18mm 10mm;
  }
}
