/* FeeReport.css */
.fee-report-container {
  background: white;
  padding: 12mm;
  margin: 0 auto;
  width: 297mm; /* A4 landscape width */
  min-height: 210mm; /* A4 landscape height */
  box-sizing: border-box;
  font-family: "Arial", sans-serif;
  color: black;
  -webkit-print-color-adjust: exact !important;
  print-color-adjust: exact !important;
  position: relative;
}

.report-header {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  border-bottom: 3px solid #1976d2;
  padding-bottom: 15px;
  margin-bottom: 25px;
}

.report-title-group {
  text-align: left;
}

.report-title-group h1 {
  margin: 0;
  color: #1976d2;
  font-size: 28px;
  font-weight: 700;
  letter-spacing: 1px;
}

.report-title-group h2 {
  margin: 5px 0;
  color: #333;
  font-size: 20px;
  font-weight: 600;
}

.report-title-group h3 {
  margin: 5px 0 0 0;
  color: #666;
  font-size: 16px;
  font-weight: 500;
}

.report-meta {
  text-align: right;
  font-size: 12px;
  color: #666;
}

.report-meta p {
  margin: 3px 0;
  line-height: 1.4;
}

.report-section {
  margin-bottom: 30px;
}

.report-section h4 {
  color: #1976d2;
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 15px;
  padding-bottom: 8px;
  border-bottom: 2px solid #1976d2;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.report-section h5 {
  color: #333;
  font-size: 16px;
  font-weight: 600;
  margin: 20px 0 12px 0;
  padding-left: 8px;
  border-left: 4px solid #4caf50;
}

.summary-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 15px;
  margin-bottom: 20px;
}

.stat-item {
  background: linear-gradient(135deg, #f8f9fa, #e9ecef);
  padding: 15px;
  border-radius: 8px;
  border: 2px solid #dee2e6;
  display: flex;
  flex-direction: column;
  gap: 8px;
  text-align: center;
}

.stat-label {
  font-weight: 600;
  color: #666;
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.stat-value {
  font-weight: 700;
  color: #1976d2;
  font-size: 16px;
  line-height: 1.2;
}

.stat-value.positive {
  color: #4caf50;
}

.stat-value.negative {
  color: #f44336;
}

.report-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
  font-size: 11px;
  -webkit-print-color-adjust: exact !important;
  print-color-adjust: exact !important;
}

.report-table th,
.report-table td {
  border: 1px solid #ccc;
  padding: 8px 6px;
  text-align: left;
  vertical-align: middle;
}

.report-table th {
  background-color: #1976d2;
  color: white;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 10px;
  letter-spacing: 0.3px;
  -webkit-print-color-adjust: exact !important;
  print-color-adjust: exact !important;
}

.report-table tr:nth-child(even) {
  background-color: #f8f9fa;
}

.report-table tr:hover {
  background-color: #e9ecef;
}

/* Prevent page breaks from cutting rows */
.report-table-row {
  page-break-inside: avoid !important;
  break-inside: avoid !important;
}

.report-table tbody tr {
  page-break-inside: avoid !important;
  break-inside: avoid !important;
}

.status-badge {
  display: inline-block;
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 10px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.status-badge.completed {
  background-color: #4caf50;
  color: white;
}

.status-badge.uncompleted {
  background-color: #f44336;
  color: white;
}

.status-badge.partial {
  background-color: #ff9800;
  color: white;
}

.report-footer {
  text-align: center;
  margin-top: 40px;
  padding-top: 20px;
  border-top: 2px solid #dee2e6;
}

.report-footer p {
  margin: 0 0 30px 0;
  color: #666;
  font-size: 12px;
}

.signature-area {
  margin-top: 50px;
  display: inline-block;
}

.signature-line {
  border-top: 1px solid black;
  width: 200px;
  margin-bottom: 5px;
}

.signature-area p {
  margin: 0;
  font-size: 12px;
  color: #333;
}

.download-pdf-btn {
  position: fixed;
  top: 20px;
  right: 20px;
  background: linear-gradient(135deg, #4caf50, #388e3c);
  color: white;
  border: none;
  border-radius: 8px;
  padding: 12px 20px;
  font-weight: 600;
  font-size: 14px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 8px;
  box-shadow: 0 4px 12px rgba(76, 175, 80, 0.3);
  transition: all 0.3s ease;
  z-index: 1000;
}

.download-pdf-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(76, 175, 80, 0.4);
}

/* Modal Overlay and Content */
.fee-report-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 2000;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow-y: auto;
  padding: 20px;
}

.fee-report-modal-content {
  background: #fff;
  border-radius: 16px;
  max-width: 95vw;
  max-height: 95vh;
  overflow-y: auto;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  position: relative;
}

.fee-report-modal-content .fee-report-container {
  width: 100%;
  max-width: 210mm;
  margin: 0 auto;
  padding: 15mm;
  border-radius: 0;
  box-shadow: none;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .fee-report-modal-content .fee-report-container {
    padding: 10mm;
  }

  .summary-stats {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 12px;
  }

  .report-table {
    font-size: 10px;
  }

  .report-table th,
  .report-table td {
    padding: 6px 4px;
  }
}

@media (max-width: 900px) {
  .fee-report-modal-content .fee-report-container {
    padding: 8mm;
  }

  .report-header {
    flex-direction: column;
    gap: 15px;
    align-items: flex-start;
  }

  .report-title-group h1 {
    font-size: 24px;
  }

  .report-title-group h2 {
    font-size: 18px;
  }

  .report-title-group h3 {
    font-size: 14px;
  }

  .report-meta {
    text-align: left;
    font-size: 11px;
  }

  .summary-stats {
    grid-template-columns: 1fr;
    gap: 10px;
  }

  .stat-item {
    padding: 12px;
  }

  .stat-value {
    font-size: 14px;
  }

  .report-table {
    font-size: 9px;
  }

  .report-table th,
  .report-table td {
    padding: 4px 2px;
  }
}

@media (max-width: 600px) {
  .fee-report-modal-content {
    margin: 10px;
    max-width: calc(100vw - 20px);
  }

  .fee-report-modal-content .fee-report-container {
    padding: 5mm;
    width: 100%;
    min-width: 0;
  }

  .report-title-group h1 {
    font-size: 20px;
  }

  .report-title-group h2 {
    font-size: 16px;
  }

  .report-title-group h3 {
    font-size: 12px;
  }

  .report-section h4 {
    font-size: 16px;
  }

  .report-section h5 {
    font-size: 14px;
  }

  .stat-item {
    padding: 10px;
  }

  .stat-label {
    font-size: 11px;
  }

  .stat-value {
    font-size: 12px;
  }

  .report-table {
    font-size: 8px;
    display: block;
    overflow-x: auto;
    white-space: nowrap;
  }

  .report-table thead,
  .report-table tbody,
  .report-table tr {
    display: table;
    width: 100%;
    table-layout: fixed;
  }

  .download-pdf-btn {
    top: 10px;
    right: 10px;
    padding: 10px 16px;
    font-size: 12px;
  }
}

/* Print Styles */
@media print {
  .fee-report-modal-overlay,
  .fee-report-modal-content {
    position: static !important;
    background: none !important;
    box-shadow: none !important;
    border-radius: 0 !important;
    max-width: none !important;
    max-height: none !important;
    overflow: visible !important;
    padding: 0 !important;
    margin: 0 !important;
  }

  .fee-report-container {
    width: 100% !important;
    min-height: auto !important;
    padding: 10mm !important;
    margin: 0 !important;
    box-shadow: none !important;
  }

  .download-pdf-btn {
    display: none !important;
  }

  .report-table {
    box-shadow: none !important;
    border: 1px solid #000 !important;
  }

  .report-table th {
    background: #f0f0f0 !important;
    color: #000 !important;
    border: 1px solid #000 !important;
  }

  .report-table td {
    border: 1px solid #000 !important;
  }

  /* Prevent page breaks from cutting rows when printing */
  .report-table tbody tr {
    page-break-inside: avoid !important;
    break-inside: avoid !important;
    -webkit-region-break-inside: avoid !important;
  }

  .report-table-row {
    page-break-inside: avoid !important;
    break-inside: avoid !important;
    -webkit-region-break-inside: avoid !important;
  }

  .stat-item {
    border: 1px solid #000 !important;
    background: #f9f9f9 !important;
  }

  .report-header {
    border-bottom: 2px solid #000 !important;
  }

  .report-footer {
    border-top: 1px solid #000 !important;
  }

  /* Ensure all text is visible */
  .report-content,
  .report-section,
  .report-info,
  .summary-stats,
  .stat-item,
  .report-table,
  .report-table th,
  .report-table td,
  .stat-label,
  .stat-value {
    color: #000 !important;
    background: transparent !important;
  }
}